import React from 'react'
import {Helmet} from "react-helmet"
import {Link} from "react-router-dom"
// import Button from ""

function Home() {
    return (
        <>
        <Helmet>
        <title>
            Quiz App - Home
        </title>
        </Helmet>
        <div className="App">
            <section>
                <div>
                    <h1>Quiz App.</h1>
                    <ul style={{marginRight: "2.3rem"}}>
                        <li> <Link to="/play"> PLAY</Link></li>
                    </ul>
                </div>
            </section>
            
           
           
            
            
        </div>
    
        
        </>
    )
}

export default Home
